@import "../style/global.scss";

.dialogOverlay {
    display: flex;
    flex-wrap: wrap;
    position: fixed;
    width: 100%;
    background: rgba(0, 0, 0, 0.25);
    height: 100%;
    top: 0;
    left: 0;
    align-content: center;
    padding: 8px 28px;
    z-index: 2;
    justify-content: center;
    @media (min-width: $screen-md) {
        padding: 36px;
    }

    &.hidden,
    .hidden {
        width: 0;
        height: 0;
        border: none;
        position: absolute;
        padding: 0;
        overflow: hidden;
    }

    .dialogContent {
        @include calc("max-height", "100% - 128px");
        @include box-shadow(
            0 11px 15px -7px rgba(0, 0, 0, 0.2),
            0 24px 38px 3px rgba(0, 0, 0, 0.14),
            0 9px 46px 8px rgba(0, 0, 0, 0.12)
        );
        max-width: 540px;
        width: 100%;
        background-color: #fff;
        overflow: auto;
        position: relative;
        &:not(.noPadding) {
            padding: 30px 15px;
            @media only screen and (min-width: $screen-sm) {
                padding: 60px 40px;
            }
        }
        @media (min-width: $screen-md) {
            @include calc("width", "100% - 128px");
            max-height: 100%;
        }
        .closeButton {
            background: none;
            border: none;
            color: $color-primary;
            display: inline-block;
            position: absolute;
            right: 7px;
            top: 4px;
            text-decoration: underline;
            font-size: 19px;
            cursor: pointer;
            @media only screen and (min-width: $screen-sm) {
                right: 15px;
                top: 10px;
            }
            &:hover {
                text-decoration: none;
            }
            img {
                width: 17px;
                height: 17px;
            }
        }
    }
}
